.basic-user-card {
  display: -ms-flexbox;
  display: flex;
  -ms-flex: 1 0;
      flex: 1 0;
  margin: 0;
  padding: 0.6em 1em;
}
.basic-user-card-collapsed-content {
  margin-left: 0.7em;
  text-align: left;
  -ms-flex: 1;
      flex: 1;
  min-width: 0;
}
.basic-user-card-user-name img {
  -o-object-fit: contain;
     object-fit: contain;
  height: 16px;
  width: 16px;
  vertical-align: middle;
}
.basic-user-card-user-name-value, .basic-user-card-screen-name {
  display: inline-block;
  max-width: 100%;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.basic-user-card-expanded-content {
  -ms-flex: 1;
      flex: 1;
  margin-left: 0.7em;
  min-width: 0;
}