.poll .votes {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  margin: 0 0 0.5em;
}
.poll .poll-option {
  margin: 0.75em 0.5em;
}
.poll .option-result {
  height: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  position: relative;
  color: #b9b9ba;
  color: var(--lightText, #b9b9ba);
}
.poll .option-result-label {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  padding: 0.1em 0.25em;
  z-index: 1;
  word-break: break-word;
}
.poll .result-percentage {
  width: 3.5em;
  -ms-flex-negative: 0;
      flex-shrink: 0;
}
.poll .result-fill {
  height: 100%;
  position: absolute;
  color: #b9b9ba;
  color: var(--pollText, #b9b9ba);
  background-color: #151e2a;
  background-color: var(--poll, #151e2a);
  border-radius: 10px;
  border-radius: var(--panelRadius, 10px);
  top: 0;
  left: 0;
  transition: width 0.5s;
}
.poll .option-vote {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
}
.poll input {
  width: 3.5em;
}
.poll .footer {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
}
.poll.loading * {
  cursor: progress;
}
.poll .poll-vote-button {
  padding: 0 0.5em;
  margin-right: 0.5em;
}