.search-result-heading {
  color: rgba(185, 185, 186, 0.5);
  color: var(--faint, rgba(185, 185, 186, 0.5));
  padding: 0.75rem;
  text-align: center;
}
@media all and (max-width: 800px) {
.search-nav-heading .tab-switcher .tabs .tab-wrapper {
    display: block;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex: 1 1 auto;
        flex: 1 1 auto;
    text-align: center;
}
}
.search-result {
  box-sizing: border-box;
  border-bottom: 1px solid;
  border-color: #222;
  border-color: var(--border, #222);
}
.search-result-footer {
  border-width: 1px 0 0 0;
  border-style: solid;
  border-color: var(--border, #222);
  padding: 10px;
  background-color: #182230;
  background-color: var(--panel, #182230);
}
.search-input-container {
  padding: 0.8rem;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
}
.search-input-container .search-input {
  width: 100%;
  line-height: 1.125rem;
  font-size: 1rem;
  padding: 0.5rem;
  box-sizing: border-box;
}
.search-input-container .search-button {
  margin-left: 0.5em;
}
.loading-icon {
  padding: 1em;
}
.trend {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
}
.trend .hashtag {
  -ms-flex: 1 1 auto;
      flex: 1 1 auto;
  color: #b9b9ba;
  color: var(--text, #b9b9ba);
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.trend .count {
  -ms-flex: 0 0 auto;
      flex: 0 0 auto;
  width: 2rem;
  font-size: 1.5rem;
  line-height: 2.25rem;
  font-weight: 500;
  text-align: center;
  color: #b9b9ba;
  color: var(--text, #b9b9ba);
}