.chat-view {
  display: -ms-flexbox;
  display: flex;
  height: 100%;
}
.chat-view .chat-view-inner {
  height: auto;
  width: 100%;
  overflow: visible;
  display: -ms-flexbox;
  display: flex;
}
.chat-view .chat-view-body {
  box-sizing: border-box;
  background-color: var(--chatBg, #121a24);
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  width: 100%;
  overflow: visible;
  min-height: calc(100vh - var(--navbar-height));
  margin: 0 0 0 0;
  border-radius: 10px 10px 0 0;
  border-radius: var(--panelRadius, 10px) var(--panelRadius, 10px) 0 0;
}
.chat-view .chat-view-body::after {
  border-radius: 0;
}
.chat-view .message-list {
  padding: 0 0.8em;
  height: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: end;
      justify-content: end;
}
.chat-view .footer {
  position: -webkit-sticky;
  position: sticky;
  bottom: 0;
  background-color: #121a24;
  background-color: var(--bg, #121a24);
  z-index: 1;
}
.chat-view .chat-view-heading {
  -ms-grid-columns: auto minmax(50%, 1fr);
      grid-template-columns: auto minmax(50%, 1fr);
}
.chat-view .go-back-button {
  text-align: center;
  line-height: 1;
  height: 100%;
  -ms-flex-item-align: start;
      -ms-grid-row-align: start;
      align-self: start;
  width: var(--__panel-heading-height-inner);
}
.chat-view .jump-to-bottom-button {
  width: 2.5em;
  height: 2.5em;
  border-radius: 100%;
  position: absolute;
  right: 1.3em;
  top: -3.2em;
  background-color: #182230;
  background-color: var(--btn, #182230);
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  box-shadow: 0 1px 1px rgba(0, 0, 0, 0.3), 0 2px 4px rgba(0, 0, 0, 0.3);
  z-index: 10;
  transition: 0.35s all;
  transition-timing-function: cubic-bezier(0, 1, 0.5, 1);
  opacity: 0;
  visibility: hidden;
  cursor: pointer;
}
.chat-view .jump-to-bottom-button.visible {
  opacity: 1;
  visibility: visible;
}
.chat-view .jump-to-bottom-button i {
  font-size: 1em;
  color: #b9b9ba;
  color: var(--text, #b9b9ba);
}
.chat-view .jump-to-bottom-button .unread-message-count {
  font-size: 0.8em;
  left: 50%;
  margin-top: -1rem;
  padding: 0.1em;
  border-radius: 50px;
  position: absolute;
}
.chat-view .jump-to-bottom-button .chat-loading-error {
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: end;
      align-items: flex-end;
  height: 100%;
}
.chat-view .jump-to-bottom-button .chat-loading-error .error {
  width: 100%;
}