.chat-title {
  display: -ms-flexbox;
  display: flex;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  --emoji-size: 14px;
}
.chat-title .username {
  max-width: 100%;
  text-overflow: ellipsis;
  white-space: nowrap;
  display: inline;
  word-wrap: break-word;
  overflow: hidden;
}
.chat-title .avatar-container {
  -ms-flex-item-align: center;
      -ms-grid-row-align: center;
      align-self: center;
  line-height: 1;
}
.chat-title .titlebar-avatar {
  margin-right: 0.5em;
  height: 1.5em;
  width: 1.5em;
  border-radius: 10px;
  border-radius: var(--avatarAltRadius, 10px);
}
.chat-title .titlebar-avatar.animated::before {
  display: none;
}