.dark-overlay::before {
  bottom: 0;
  content: " ";
  display: block;
  cursor: default;
  left: 0;
  position: fixed;
  right: 0;
  top: 0;
  background: rgba(27, 31, 35, 0.5);
  z-index: 99;
}
.dialog-modal.panel {
  top: 0;
  left: 50%;
  max-height: 80vh;
  max-width: 90vw;
  margin: 15vh auto;
  position: fixed;
  transform: translateX(-50%);
  z-index: 999;
  cursor: default;
  display: block;
  background-color: #121a24;
  background-color: var(--bg, #121a24);
}
.dialog-modal.panel .dialog-modal-heading .title {
  text-align: center;
}
.dialog-modal.panel .dialog-modal-content {
  margin: 0;
  padding: 1rem 1rem;
  background-color: #121a24;
  background-color: var(--bg, #121a24);
  white-space: normal;
}
.dialog-modal.panel .dialog-modal-footer {
  margin: 0;
  padding: 0.5em 0.5em;
  background-color: #121a24;
  background-color: var(--bg, #121a24);
  border-top: 1px solid #222;
  border-top: 1px solid var(--border, #222);
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: end;
      justify-content: flex-end;
}
.dialog-modal.panel .dialog-modal-footer button {
  width: auto;
  margin-left: 0.5rem;
}