.link-preview-card {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  cursor: pointer;
  overflow: hidden;
  margin-top: 0.5em;
  color: #b9b9ba;
  color: var(--text, #b9b9ba);
  border-style: solid;
  border-width: 1px;
  border-radius: 10px;
  border-radius: var(--attachmentRadius, 10px);
  border-color: #222;
  border-color: var(--border, #222);
}
.link-preview-card .card-image {
  -ms-flex-negative: 0;
      flex-shrink: 0;
  width: 120px;
  max-width: 25%;
}
.link-preview-card .card-image img {
  width: 100%;
  height: 100%;
  -o-object-fit: cover;
     object-fit: cover;
  border-radius: 10px;
  border-radius: var(--attachmentRadius, 10px);
}
.link-preview-card .card-content {
  max-height: 100%;
  margin: 0.5em;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}
.link-preview-card .card-host {
  font-size: 0.85em;
}
.link-preview-card .card-description {
  margin: 0.5em 0 0 0;
  overflow: hidden;
  text-overflow: ellipsis;
  word-break: break-word;
  line-height: 1.2em;
  max-height: calc(1.2em * 3 - 1px);
}
.link-preview-card .nsfw-alert {
  margin: 2em 0;
}