.mrf-section {
  margin: 1em;
}
.mrf-section table {
  width: 100%;
  text-align: left;
  padding-left: 10px;
  padding-bottom: 20px;
}
.mrf-section table th, .mrf-section table td {
  width: 180px;
  max-width: 360px;
  overflow: hidden;
  vertical-align: text-top;
}
.mrf-section table th + th, .mrf-section table td + td {
  width: auto;
}