.panel-loading {
  display: -ms-flexbox;
  display: flex;
  height: 100%;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  font-size: 2em;
  color: #b9b9ba;
  color: var(--text, #b9b9ba);
}
.panel-loading .loading-text svg {
  line-height: 0;
  vertical-align: middle;
  color: #b9b9ba;
  color: var(--text, #b9b9ba);
}