.chat-list-item {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  padding: 0.75em;
  height: 5em;
  overflow: hidden;
  box-sizing: border-box;
  cursor: pointer;
}
.chat-list-item :focus {
  outline: none;
}
.chat-list-item:hover {
  background-color: var(--selectedPost, #151e2a);
  box-shadow: 0 0 3px 1px rgba(0, 0, 0, 0.1);
}
.chat-list-item .chat-list-item-left {
  margin-right: 1em;
}
.chat-list-item .chat-list-item-center {
  width: 100%;
  box-sizing: border-box;
  overflow: hidden;
  word-wrap: break-word;
}
.chat-list-item .heading {
  width: 100%;
  display: -ms-inline-flexbox;
  display: inline-flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  line-height: 1em;
}
.chat-list-item .heading-right {
  white-space: nowrap;
}
.chat-list-item .name-and-account-name {
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
  -ms-flex-negative: 1;
      flex-shrink: 1;
  line-height: var(--post-line-height);
}
.chat-list-item .chat-preview {
  display: -ms-inline-flexbox;
  display: inline-flex;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  margin: 0.35em 0;
  color: #b9b9ba;
  color: var(--faint, #b9b9ba);
  width: 100%;
}
.chat-list-item a {
  color: var(--faintLink, #d8a070);
  text-decoration: none;
  pointer-events: none;
}
.chat-list-item:hover .animated.avatar canvas {
  display: none;
}
.chat-list-item:hover .animated.avatar img {
  visibility: visible;
}
.chat-list-item .Avatar {
  border-radius: 10px;
  border-radius: var(--avatarAltRadius, 10px);
}
.chat-list-item .chat-preview-body {
  --emoji-size: 1.4em;
}
.chat-list-item .time-wrapper {
  line-height: var(--post-line-height);
}
.chat-list-item .chat-preview-body {
  padding-right: 1em;
}