.user-list-popover {
  padding: 0.5em;
  --emoji-size: 16px;
}
.user-list-popover .user-list-row {
  padding: 0.25em;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
}
.user-list-popover .user-list-row .user-list-names {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
