.avatars {
  display: -ms-flexbox;
  display: flex;
  margin: 0;
  padding: 0;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  height: 24px;
}
.avatars .avatars-item {
  margin: 0 0 5px 5px;
}
.avatars .avatars-item:first-child {
  padding-left: 5px;
}
.avatars .avatars-item .avatar-small {
  border-radius: 10px;
  border-radius: var(--avatarAltRadius, 10px);
  height: 24px;
  width: 24px;
}