.password-reset-form {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  margin: 0.6em;
}
.password-reset-form .container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex: 1 0;
      flex: 1 0;
  -ms-flex-direction: column;
      flex-direction: column;
  margin-top: 0.6em;
  max-width: 18rem;
}
.password-reset-form .container > * {
  min-width: 0;
}
.password-reset-form .form-group {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  margin-bottom: 1em;
  padding: 0.3em 0;
  line-height: 1.85em;
}
.password-reset-form .error {
  text-align: center;
  animation-name: shakeError;
  animation-duration: 0.4s;
  animation-timing-function: ease-in-out;
}
.password-reset-form .alert {
  padding: 0.5em;
  margin: 0.3em 0 1em;
}
.password-reset-form .password-reset-required {
  background-color: var(--alertError, rgba(211, 16, 20, 0.5));
  padding: 10px 0;
}
.password-reset-form .notice-dismissible {
  padding-right: 2rem;
}
.password-reset-form .dismiss {
  cursor: pointer;
}