.chat-new .input-wrap {
  display: -ms-flexbox;
  display: flex;
  margin: 0.7em 0.5em 0.7em 0.5em;
}
.chat-new .input-wrap input {
  width: 100%;
}
.chat-new .search-icon {
  margin-right: 0.3em;
}
.chat-new .member-list {
  padding-bottom: 0.7rem;
}
.chat-new .basic-user-card:hover {
