  background-color: var(--selectedPost, #151e2a);
}
.chat-new .go-back-button {
  text-align: center;
  line-height: 1;
  height: 100%;
  -ms-flex-item-align: start;
      -ms-grid-row-align: start;
      align-self: start;
  width: var(--__panel-heading-height-inner);
}