.user-reporting-panel {
  width: 90vw;
  max-width: 700px;
  min-height: 20vh;
  max-height: 80vh;
}
.user-reporting-panel .panel-body {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column-reverse;
      flex-direction: column-reverse;
  border-top: 1px solid;
  border-color: #222;
  border-color: var(--border, #222);
  overflow: hidden;
}
.user-reporting-panel-left {
  padding: 1.1em 0.7em 0.7em;
  line-height: var(--post-line-height);
  box-sizing: border-box;
}
.user-reporting-panel-left > div {
  margin-bottom: 1em;
}
.user-reporting-panel-left > div:last-child {
  margin-bottom: 0;
}
.user-reporting-panel-left p {
  margin-top: 0;
}
.user-reporting-panel-left textarea.form-control {
  line-height: 16px;
  resize: none;
  overflow: hidden;
  transition: min-height 200ms 100ms;
  min-height: 44px;
  width: 100%;
}
.user-reporting-panel-left .btn {
  min-width: 10em;
  padding: 0 2em;
}
.user-reporting-panel-left .alert {
  margin: 1em 0 0 0;
  line-height: 1.3em;
}
.user-reporting-panel-right {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  overflow-y: auto;
}
.user-reporting-panel-sitem {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  /* TODO cleanup this */
}
.user-reporting-panel-sitem > .Status {
  -ms-flex: 1;
      flex: 1;
}
.user-reporting-panel-sitem > .checkbox {
  margin: 0.75em;
}
@media all and (min-width: 801px) {
.user-reporting-panel .panel-body {
    -ms-flex-direction: row;
        flex-direction: row;
}
.user-reporting-panel-left {
    width: 50%;
    max-width: 320px;
    border-right: 1px solid;
    border-color: #222;
    border-color: var(--border, #222);
    padding: 1.1em;
}
.user-reporting-panel-left > div {
    margin-bottom: 2em;
}
.user-reporting-panel-right {
    width: 50%;
    -ms-flex: 1 1 auto;
        flex: 1 1 auto;
    margin-bottom: 12px;
}
}