@charset "UTF-8";
.registration-form {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  margin: 0.6em;
}
.registration-form .container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
}
.registration-form .container > * {
  min-width: 0;
}
.registration-form .terms-of-service {
  -ms-flex: 0 1 50%;
      flex: 0 1 50%;
  margin: 0.8em;
}
.registration-form .text-fields {
  margin-top: 0.6em;
  -ms-flex: 1 0;
      flex: 1 0;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}
.registration-form textarea {
  min-height: 100px;
  resize: vertical;
}
.registration-form .form-group {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  padding: 0.3em 0;
  line-height: 2;
  margin-bottom: 1em;
}
.registration-form .form-group--error {
  animation-name: shakeError;
  animation-duration: 0.6s;
  animation-timing-function: ease-in-out;
}
.registration-form .form-group--error .form--label {
  color: #f04124;
  color: var(--cRed, #f04124);
}
.registration-form .form-error {
  margin-top: -0.7em;
  text-align: left;
}
.registration-form .form-error span {
  font-size: 0.85em;
}
.registration-form .form-error ul {
  list-style: none;
  padding: 0 0 0 5px;
  margin-top: 0;
}
.registration-form .form-error ul li::before {
  content: "\2022   ";
}
.registration-form form textarea {
  line-height: 16px;
  resize: vertical;
}
.registration-form .captcha {
  max-width: 350px;
  margin-bottom: 0.4em;
}
.registration-form .btn {
  margin-top: 0.6em;
  height: 2em;
}
.registration-form .error {
  text-align: center;
}
@media all and (max-width: 800px) {
.registration-form .container {
    -ms-flex-direction: column-reverse;
        flex-direction: column-reverse;
}
}