.floating-shout {
  position: fixed;
  bottom: 0.5em;
  z-index: 1000;
  max-width: 25em;
}
.floating-shout.-left {
  left: 0.5em;
}
.floating-shout:not(.-left) {
  right: 0.5em;
}
.shout-panel .shout-heading {
  cursor: pointer;
}
.shout-panel .shout-heading .icon {
  color: #b9b9ba;
  color: var(--panelText, #b9b9ba);
  margin-right: 0.5em;
}
.shout-panel .shout-heading .title {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;
}
.shout-panel .shout-window {
  overflow-y: auto;
  overflow-x: hidden;
  max-height: 20em;
}
.shout-panel .shout-window-container {
  height: 100%;
}
.shout-panel .shout-message {
  display: -ms-flexbox;
  display: flex;
  padding: 0.2em 0.5em;
}
.shout-panel .shout-avatar img {
  height: 24px;
  width: 24px;
  border-radius: 4px;
  border-radius: var(--avatarRadius, 4px);
  margin-right: 0.5em;
  margin-top: 0.25em;
}
.shout-panel .shout-input {
  display: -ms-flexbox;
  display: flex;
}
.shout-panel .shout-input textarea {
  -ms-flex: 1;
      flex: 1;
  margin: 0.6em;
  min-height: 3.5em;
  resize: none;
}
.shout-panel .shout-panel .title {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
}