.MobileNav .mobile-nav {
  display: -ms-grid;
  display: grid;
  line-height: var(--navbar-height);
  -ms-grid-rows: 50px;
      grid-template-rows: 50px;
  -ms-grid-columns: 2fr auto;
      grid-template-columns: 2fr auto;
  width: 100%;
  box-sizing: border-box;
}
.MobileNav .mobile-nav a {
  color: var(--topBarLink, #d8a070);
}
.MobileNav .mobile-inner-nav {
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
}
.MobileNav .mobile-nav-button {
  display: inline-block;
  text-align: center;
  padding: 0 1em;
  position: relative;
  cursor: pointer;
}
.MobileNav .site-name {
  padding: 0 0.3em;
  display: inline-block;
}
.MobileNav .item {
  /* moslty just to get rid of extra whitespaces */
  display: -ms-flexbox;
  display: flex;
}
.MobileNav .alert-dot {
  border-radius: 100%;
  height: 8px;
  width: 8px;
  position: absolute;
  left: calc(50% - 4px);
  top: calc(50% - 4px);
  margin-left: 6px;
  margin-top: -6px;
  background-color: #ff0000;
  background-color: var(--badgeNotification, #ff0000);
}
.MobileNav .mobile-notifications-drawer {
  width: 100%;
  height: 100vh;
  overflow-x: hidden;
  position: fixed;
  top: 0;
  left: 0;
  box-shadow: 1px 1px 4px rgba(0, 0, 0, 0.6);
  box-shadow: var(--panelShadow);
  transition-property: transform;
  transition-duration: 0.25s;
  transform: translateX(0);
  z-index: 1001;
  -webkit-overflow-scrolling: touch;
}
.MobileNav .mobile-notifications-drawer.-closed {
  transform: translateX(100%);
  box-shadow: none;
}
.MobileNav .mobile-notifications-header {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: justify;
      justify-content: space-between;
  z-index: 1;
  width: 100%;
  height: 50px;
  line-height: 50px;
  position: absolute;
  color: var(--topBarText);
  background-color: #182230;
  background-color: var(--topBar, #182230);
  box-shadow: 0px 0px 4px rgba(0, 0, 0, 0.6);
  box-shadow: var(--topBarShadow);
}
.MobileNav .mobile-notifications-header .title {
  font-size: 1.3em;
  margin-left: 0.6em;
}
.MobileNav .mobile-notifications {
  margin-top: 50px;
  width: 100vw;
  height: calc(100vh - var(--navbar-height));
  overflow-x: hidden;
  overflow-y: scroll;
  color: #b9b9ba;
  color: var(--text, #b9b9ba);
  background-color: #121a24;
  background-color: var(--bg, #121a24);
}
.MobileNav .mobile-notifications .notifications {
  padding: 0;
  border-radius: 0;
  box-shadow: none;
}
.MobileNav .mobile-notifications .notifications .panel {
  border-radius: 0;
  margin: 0;
  box-shadow: none;
}
.MobileNav .mobile-notifications .notifications .panel:after {
  border-radius: 0;
}
.MobileNav .mobile-notifications .notifications .panel .panel-heading {
  border-radius: 0;
  box-shadow: none;
}