/* TODO fix order of styles */
label.Select {
  padding: 0;
}
label.Select select {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  background: transparent;
  border: none;
  color: #b9b9ba;
  color: var(--inputText, --text, #b9b9ba);
  margin: 0;
  padding: 0 2em 0 0.2em;
  font-family: sans-serif;
  font-family: var(--inputFont, sans-serif);
  font-size: 1em;
  width: 100%;
  z-index: 1;
  height: 2em;
  line-height: 16px;
}
label.Select .select-down-icon {
  position: absolute;
  top: 0;
  bottom: 0;
  right: 5px;
  height: 100%;
  width: 0.875em;
  color: #b9b9ba;
  color: var(--inputText, #b9b9ba);
  line-height: 2;
  z-index: 0;
  pointer-events: none;
}