.Flash {
  display: inline-block;
  width: 100%;
  height: 100%;
  position: relative;
}
.Flash .player {
  height: 100%;
  width: 100%;
}
.Flash .placeholder {
  height: 100%;
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  background: var(--bg);
  color: var(--link);
}
.Flash .hider {
  top: 0;
}
.Flash .label {
  text-align: center;
  -ms-flex: 1 1 0px;
      flex: 1 1 0;
  line-height: 1.2;
  white-space: normal;
  word-wrap: normal;
}
.Flash .hidden {
  display: none;
  visibility: "hidden";
}