@charset "UTF-8";
.checkbox {
  position: relative;
  display: inline-block;
  min-height: 1.2em;
}
.checkbox-indicator {
  position: relative;
  padding-left: 1.2em;
}
.checkbox-indicator::before {
  position: absolute;
  right: 0;
  top: 0;
  display: block;
  content: "\2713";
  transition: color 200ms;
  width: 1.1em;
  height: 1.1em;
  border-radius: 2px;
  border-radius: var(--checkboxRadius, 2px);
  box-shadow: 0px 0px 2px black inset;
  box-shadow: var(--inputShadow);
  background-color: #182230;
  background-color: var(--input, #182230);
  vertical-align: top;
  text-align: center;
  line-height: 1.1em;
  font-size: 1.1em;
  color: transparent;
  overflow: hidden;
  box-sizing: border-box;
}
.checkbox.disabled .checkbox-indicator::before,
.checkbox.disabled .label {
  opacity: 0.5;
}
.checkbox.disabled .label {
  color: rgba(185, 185, 186, 0.5);
  color: var(--faint, rgba(185, 185, 186, 0.5));
}
.checkbox input[type=checkbox] {
  display: none;
}
.checkbox input[type=checkbox]:checked + .checkbox-indicator::before {
  color: #b9b9ba;
  color: var(--inputText, #b9b9ba);
}
.checkbox input[type=checkbox]:indeterminate + .checkbox-indicator::before {
  content: "\2013";
  color: #b9b9ba;
  color: var(--inputText, #b9b9ba);
}
.checkbox > span {
  margin-left: 0.5em;
}