.login-form {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  padding: 0.6em;
}
.login-form .btn {
  min-height: 2em;
  width: 10em;
}
.login-form .register {
  -ms-flex: 1 1;
      flex: 1 1;
}
.login-form .login-bottom {
  margin-top: 1em;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: justify;
      justify-content: space-between;
}
.login-form .form-group {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  padding: 0.3em 0.5em 0.6em;
  line-height: 24px;
}
.login-form .form-bottom {
  display: -ms-flexbox;
  display: flex;
  padding: 0.5em;
  height: 32px;
}
.login-form .form-bottom button {
  width: 10em;
}
.login-form .form-bottom p {
  margin: 0.35em;
  padding: 0.35em;
  display: -ms-flexbox;
  display: flex;
}
.login-form .error {
  text-align: center;
  animation-name: shakeError;
  animation-duration: 0.4s;
  animation-timing-function: ease-in-out;
}