.emoji-reactions {
  display: -ms-flexbox;
  display: flex;
  margin-top: 0.25em;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}
.unicode-emoji {
  font-size: 210%;
}
.emoji-reaction {
  padding: 0 0.5em;
  margin-right: 0.5em;
  margin-top: 0.5em;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  box-sizing: border-box;
}
.emoji-reaction .reaction-emoji {
  width: 2.55em !important;
  margin-right: 0.25em;
}
.emoji-reaction:focus {
  outline: none;
}
.emoji-reaction.not-clickable {
  cursor: default;
}
.emoji-reaction.not-clickable:hover {
  box-shadow: 0px 0px 2px 0px black, 0px 1px 0px 0px rgba(255, 255, 255, 0.2) inset, 0px -1px 0px 0px rgba(0, 0, 0, 0.2) inset;
  box-shadow: var(--buttonShadow);
}
.emoji-reaction-expand {
  padding: 0 0.5em;
  margin-right: 0.5em;
  margin-top: 0.5em;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
}
.emoji-reaction-expand:hover {
  text-decoration: underline;
}
.picked-reaction {
  border: 1px solid var(--accent, #d8a070);
  margin-left: -1px;
  margin-right: calc(0.5em - 1px);
}